import React, { useState } from "react";

function validatePassword(pw) {
  // 6
  return /[a-zA-Z]/.test(pw) && /[0-9]/.test(pw) && /[^a-zA-Z0-9]/.test(pw) && pw.length >= 6;
}

function Login({ onLogin }) {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  // localStorage.accounts(JSON)
  const getAccounts = () => JSON.parse(localStorage.getItem("accounts") || "{}")
  const setAccounts = (obj) => localStorage.setItem("accounts", JSON.stringify(obj));

  const handleLogin = (e) => {
    e.preventDefault();
    const accounts = getAccounts();
    if (!accounts[username]) {
      setError("");
      return;
    }
    if (accounts[username] !== password) {
      setError("");
      return;
    }
    setError("");
    onLogin(username);
  };

  const handleRegister = () => {
    if (!username || !password) {
      setError("");
      return;
    }
    if (!validatePassword(password)) {
      setError("6");
      return;
    }
    const accounts = getAccounts();
    if (accounts[username]) {
      setError("");
      return;
    }
    accounts[username] = password;
    setAccounts(accounts);
    setError("");
  };

  const handleDelete = () => {
    if (!username || !password) {
      setError("");
      return;
    }
    const accounts = getAccounts();
    if (!accounts[username] || accounts[username] !== password) {
      setError("");
      return;
    }
    delete accounts[username];
    setAccounts(accounts);
    // 
    localStorage.removeItem(`products_${username}`);
    localStorage.removeItem(`history_${username}`);
    setError("");
  };

  return (
    <div style={{ padding: 24, maxWidth: 400, margin: "0 auto" }}>
      <h2></h2>
      <form onSubmit={handleLogin}>
        <input
          type="text"
          placeholder=""
          value={username}
          onChange={e => setUsername(e.target.value)}
          style={{ marginBottom: 8, width: "100%" }}
        />
        <input
          type="password"
          placeholder=""
          value={password}
          onChange={e => setPassword(e.target.value)}
          style={{ marginBottom: 8, width: "100%" }}
        />
        <button type="submit" style={{ width: "100%", marginBottom: 8 }}></button>
      </form>
      <button onClick={handleRegister} style={{ width: "100%", marginBottom: 8 }}></button>
      <button onClick={handleDelete} style={{ width: "100%", background: "#d32f2f", color: "white" }}></button>
      {error && <div style={{ color: "red", marginTop: 12 }}>{error}</div>}
    </div>
  );
}

export default Login; 